@import '../../scss/variables';

.CheckIfAvailable {
    min-height: 76vh;
    background-color: rgba(0, 0, 0, .8);

    display: flex;
    justify-content: center;
    align-items: center;

    &Content {
        min-width: 82.5rem;
        min-height: 52.5rem;
        background-color: rgb(238, 238, 238);
        border-radius: 3px;
        margin: 3rem;
        padding: 1.5rem;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
    }

    &Calendar {
        width: 100%;
        margin: 2rem 0;
    
        display: flex;
        flex-direction: column;
        align-items: center; 
    }

    &Navigation {
        display: flex;
        align-items: center;
        justify-content: space-evenly;

        width: 60%;
    }

    &Button {
        background-color: black;
        border: none;
        padding: .5rem;
        width: 4.5rem;
        border-radius: 3px; 

        display: flex;
        justify-content: center;
        align-items: center;

        transition: all .2s;

        &:hover {
            background-color: $color-primary-hovered;
            cursor: pointer;
        }
    }

    &Select {
        font-size: 2rem;
        font-family: inherit;
        margin-right: 1rem;

        background-color: transparent;
        color: black;
        border: none;
        border-bottom: 1px solid black;
        margin: 0;
    }

    &Description {
        width: 60%;
        display: flex;
        justify-content: space-evenly;
        align-items: center;

        &Container {
            display: flex;
            align-items: center;
            justify-content: center;
        }

        &Box {
            width: 2rem;
            height: 2rem;
            border: 1px solid black;
            margin-right: .5rem;
            
            &Yellow {
                @extend .CheckIfAvailableDescriptionBox;
                background-color: $color-yellow-availability;
            }

            &Red {
                @extend .CheckIfAvailableDescriptionBox;
                background-color: $color-red-availability;
            }
        }

        &Text {
            font-size: 1.5rem;
        }
    }

}


@media (max-width: 700px) {
    .CheckIfAvailableContent {
        min-width: 71rem;
    }
}

@media (max-width: 567px) {
    .CheckIfAvailableNavigation {
        width: 100%;
    }

    .CheckIfAvailableContent {
        min-width: 0;
        width: 100%;
        padding: 1.5rem .5rem;
        margin: 3rem 0;
    }

    .CheckIfAvailableDescription {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        flex-direction: column;

        width: 50%;
        height: 7rem;
    }

    .CheckIfAvailableDescriptionText {
        font-size: 1.6rem;
        font-weight: bold;
    }
}