
    .BrandToggler {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 2rem;

        &List {
            list-style: none;
            display: flex;

            &Item {
                width: 10rem;
                background-color: transparent;
                border: 2px white solid;
                border-left: none;
                
                display: flex;
                justify-content: center;
                align-items: center;

                &:first-child {
                    border-left: 2px white solid;
                }

                &Link {
                    width: 100%;
                    height: 100%;
                    padding: 1rem;
                    color: white;
                    text-decoration: none;
                    font-size: 1.4rem;
                    font-weight: bold;
                
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    
                    &Active {
                        @extend .BrandTogglerListItemLink;
                        color: black;
                        background-color: rgb(238, 238, 238);
                    }
                }
            }
        }
    }

@media (max-width: 1030px) and (min-height: 1300px) {

    .BrandToggler {
        padding: 2rem 2rem 0 2rem;

        &ListItem {
            width: 15rem;

            &Link {
                padding: 2rem;
                font-size: 2rem;
            }
        }
    }
}

@media (max-width: 1030px) {
    .BrandTogglerListItem {
        width: 15rem;

        &Link {
            padding: 2rem;
            font-size: 2rem; 
        }
    }
}

@media (max-width: 940px) {
    .BrandToggler {
        padding: 2rem;
    }
}

@media (max-width: 500px) {
    .BrandTogglerList {
        display: grid;
        grid-template-columns: repeat(2, min-content);

        &Item {
            width: 18rem;

            &:nth-child(3) {
                border-left: 2px white solid;
            }

            &:nth-child(3),
            &:nth-child(4) {
                border-top: none;
            }

            &Link {
                padding: 2.3rem;
                font-size: 2.5rem;
            }
        }
    }
}